body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body{
  font-size: 14px;
  padding: 0;
  margin: 0;
  background-color: #fafafa;
}
*{
  box-sizing: border-box;
}
header{
  background-color: #000;
  display:-webkit-flex;
  display:flex;
  -webkit-justify-content:space-between;
          justify-content:space-between;
  -webkit-align-items: center;
          align-items: center;
  height: 60px;
  color: #fff;
  padding: 0 20px;
}
header h1{
  font-weight: normal;
  font-size: 22px;
}
.main{
  width:100%;
  padding: 20px;
}
.box-outer{
  padding:10px 0;
}
.slider{
  width:100%;
  margin-bottom: 60px;
}
.slider .img-bx img{
  height:170px;
  width:auto;
  margin: auto;
}
.slider .img-bx{
  width:100%;
  text-align: center;
  max-width: 150px;
  padding: 20px 10px;
}
.box {
  background-color: white;
  margin: auto;
  border: 1px solid #efefef;
  box-shadow:0 1px 1px rgba(0,0,0,0.15);
  padding: 20px;
  -webkit-flex-basis: 24%;
          flex-basis: 24%;
  margin: 1% 0.5%;
  border-radius: 6px;
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
}
.box-in {
  -webkit-flex-direction: column;
          flex-direction: column;
  display: -webkit-flex;
  display: flex;
}
.box p{
  margin-bottom: 0;
}
.box .img-box{
  max-width: 153px;
  width: 100%;
}
.modal-content .img-box{
  text-align: center;
  width: 100%;
}
.search-form{
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}
form{
  width: 100%;
  max-width: 500px;
}
.search-form input.text{
  height: 40px;
  border-radius: 4px;
  box-shadow: none;
  border:1px solid #ddd;
  padding: 5px;
  font-size: 14px;
  width: 100%;
}
.search-form input.text:focus{
  outline: none;
  box-shadow: none;
}
@media screen and (min-width: 768px){
  .box-outer{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
  }
}
@media screen and (min-width: 768px) and (max-width: 1024px) {
  .box{
    -webkit-flex-basis: 49%;
            flex-basis: 49%;
  }
}
@media only screen and (max-width: 767px) {
  .box{
    margin:5% 0;
    -webkit-flex-basis: 49%;
            flex-basis: 49%;
  }
  .slider .img-bx img{
    height:auto;
    width:100%;
  }
  .slider .img-bx{
    max-width: 100%;
    padding: 20px 10px;
  }
  .slider{
    margin-bottom: 0;
  }
}

